
.footer {
    background-color: $color-gray-bg;
    
    .center {
        max-width: 92vw;
        border-top: 4px solid $color-gray-separator;

        @media (min-width: 1360px) {
            max-width: 1320px;
        }
    }
}

.footer_main {
    padding-top: 20px;
    padding-bottom: 118px;
    text-align: center;
    font-size: $basic-fz-mob - 2px;

    @media (min-width: 768px) {
        padding-bottom: 81px;
    }

    @media (min-width: 1024px) {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding-bottom: 40px;
    }
    
    a {
        display: block;
        font-weight: 700;
        margin-bottom: 20px;

        @media (min-width: 768px) {
            margin: 0 13px 22px;
        }
    }

    .footer_info {
        @media (min-width: 768px) {
            display: flex;
            justify-content: center;
            align-items: baseline;
            padding-top: 32px;
        }

        @media (min-width: 1024px) {
            padding: 0;
            width: 50%;
            justify-content: flex-start;
        }

        address {
            font-style: normal;
            margin: 0 10px 10px;
            font-weight: 700;
            color: $color-main;

            @media (min-width: 768px) {
                margin-left: 13px;
            }
        }

        a {
            text-transform: uppercase;
            font-size: 12px;
        }
    }

    .footer_contact {

        a[href^="tel:"] {
            color: $color-main;
        }
        @media (min-width: 768px) {
            display: flex;
            justify-content: center;
            align-items: center;
        }
        @media (min-width: 1024px) {
            order: 1;
        }
    }

    .copyright {
        font-weight: 500;
        color: $color-gray-text;
        
        p {
            margin: 0;
        }

        @media (min-width: 1024px) {
            width: 50%;
            text-align: right;
        }
    }
}


.footer.no_border {
    .center {
        border: none;
        max-width: 1360px;
    }
}

.footer.border_rad {
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
}
//footer end//