body {
    font-family: "TTNorms", Arial, Helvetica, sans-serif;
    font-size: $basic-fz-mob;
    line-height: 1.375;
}

.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    min-width: 375px;
    overflow: hidden;
  }

  .main {
    flex-grow: 1;
}


.main.margin_main {
    margin-top: 100px;

    @media (min-width: 768px) {
        margin-top: 190px;
    }

    @media (min-width: 1024px) {
        margin-top: 146px;
    }

    @media (min-width: 1360px) {
        margin-top: 140px;
    }
}

.center {
    @extend %center;
}

a {
    text-decoration: none;
    color: $color-active-regular;

    &:hover {
        color: $color-accent;
    }

    &:active {
        color: $color-active-active; //!important; 
    }
}

p {
    margin: 20px 0;
}

.circle,
.circle2 {
    position: relative;
    &::before {
        content: "";

        @media (min-width: 768px) {
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background-color: $color-gray-separator;
            position: absolute;
            left: -13px;
            top: 60%;
            transform: translateY(-50%)
        }   
    }
}

.circle2 {
    &::before {
        content: "";

        @media (min-width: 768px) {
            background-color: $color-gray-text;
            left: -13px;
            top: 57%;
            transform: translateY(-50%)
        }   
    }
}

.bullet_list {
    li {
        position: relative;
        &::before {
            content: "";
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background: $color-accent;
            position: absolute;
            left: -12px;
            top: 10px;
        }
    }
}

ol {
    counter-reset: myCounter;
    padding-left: 20px;

    li {
        list-style: none;
        margin: 0 0 15px 0;

        &::before {
            counter-increment: myCounter;
            content: counter(myCounter) ".";
            color: $color-accent;
            margin: 0 10px 0 0;
            font-weight: 700;
        }
    }
}