.services_page {
    position: relative;
    .services_bg {
        background: linear-gradient(38deg, #643E72 10%, #323264 33%, transparent 40%), linear-gradient( rgba(50, 50, 100, 0.1) 0px, #323264 500px, #323264 59.9%), url("../images/key.jpg") 50% 0% / auto 500px no-repeat;
        padding-top: 100px;

        @media (min-width: 768px) {
            padding-top: 190px;
        }
    
        @media (min-width: 1024px) {
            padding-top: 146px;
        }
    
        @media (min-width: 1360px) {
            padding-top: 140px;
            background: linear-gradient(18deg, #643E72 10%, #323264 33%, transparent 40%), linear-gradient( rgba(50, 50, 100, 0.1) 0px, #323264 500px, #323264 59.9%), url("../images/key.jpg") 50% 0% / 100% 500px no-repeat;
        }
    }

    .info_service {
        padding: 30px 0 40px;

        @media (min-width:650px) {
            text-align: center;
            max-width: 860px;
            margin: 0 auto;
        }

        @media (min-width:768px) {
            padding: 50px 0 80px;
        }

        @media (min-width:860px) {
            font-size: $basic-fz-mob + 3px;
        }

        .for_img {
            width: 92px;
            height: 92px;
            border-radius: 24px;
            padding: 14px;
            background-color: #fff;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 20px;

            @media (min-width:650px) {
                margin: 0 auto 20px;
            }
        }

        h1 {
            @extend %h1-mobile;
            margin-bottom: 20px;
        }

        p {
            margin-top: 0;

            &:last-of-type {
                margin-bottom: 40px;
            }
        }

        .button_service {
            @extend %button;
            text-align: center;
        }

        #button_form {
            display: none;
        }
    }

    .list {
        border-top: 4px solid rgba(255, 255, 255, 0.5);
        padding: 20px 0 23px;
        max-width: 1092px;
        margin: 0 auto;

        @media (min-width:768px) {
            padding-bottom: 80px;
        }

        h3 {
            @extend %h3-mobile;
            margin-bottom: 10px;

            @media (min-width:768px) {
                font-size: $basic-fz-mob + 6px;
                margin-bottom: 20px;
            }
        }
    }

    .list_service {
        padding-left: 30px;
        list-style: none;

        @media (min-width:650px) {
            //display: flex;
            columns: 2;
        }

        li {
            margin-bottom: 15px;

            @media (min-width:650px) {
                max-width: 500px;
                margin: 0 auto 15px;
                padding: 0 5px;
            }
        }
    }

    .cases_main {
        padding-top: 40px;
        margin-bottom: 20px;

        @media (min-width:700px) {
            padding-top: 60px;
        }

        @media (min-width:835px) {
            margin-bottom: 40px;
        }
    }

    .publications_main {
        border-top-left-radius: 12px;
        border-top-right-radius: 12px;
    }

    .contact_form {
        width: 375px;
        margin: 0;
        text-align: left;
        position: relative;
        border-radius: 12px;

        @media (min-width: 1024px) {
            width: 527px;
        }

        input,
        textarea {
            width: 335px;

            @media (min-width: 1024px) {
                width: 487px;
            }
        }

        span.title {
            @extend %h2-mobile;
            color: $color-accent;
            margin-bottom: 20px;
            font-weight: 500;
            display: block;

            @media (min-width: 1024px) {
                background: linear-gradient(to right, #f0145a, #ef8080);
                -webkit-background-clip: text;
                color: transparent;
                font-weight: 700;
                font-size: $basic-fz-mob + 14px;
            }
        }

        .close_form_contact {
            position: absolute;
            top: 23px;
            right: 15px;
            cursor: pointer;
            padding: 3px 4px;
            border-radius: 12px;

            @media (min-width: 1024px) {
                top: 28px
            }

            &:active {
                background-color: #EDEDF7;
            }
        }

        .close_form_contact::before {
            transform: translateY(6px) rotate(135deg);
        }
        
        .close_form_contact::after {
            transform: translateY(-6px) rotate(-135deg);
        }
        
        .close_form_contact span{
            transform: scale(0);
        }
        
        .close_form_contact span,
        .close_form_contact:after,
        .close_form_contact:before{
          background: #EC6F5D;
          content: "";
          width: 18px;
          display: block;
          height: 2px;
          margin: 4px 0;
        }
    }

    .bg_form {
        visibility: hidden;
        opacity: 0;
        position: fixed;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100vw;
        height: 100vh;
        left: 0;
        top: 0;
        z-index: -1;
        transform: translateY(-600px);
        background: linear-gradient(rgba(50, 50, 100, 0.7) 0%, rgba(50, 50, 100, 0.7) 100%), linear-gradient(#fff 0%, #fff 100%);

    }

    #button_form:checked ~ .bg_form{
        visibility: visible;
        opacity: 1;
        transform: translateY(0);
        transition: 0.4s ease;
        z-index: 15;
    }
}