$color-body-text:#000;
$color-main:#323264;
$color-main-alt:#46466e;
$color-main-soft: #686887;
$color-accent:#f0145a;
$color-active-regular:#24a3ff;
$color-active-hover:#0085ff;
$color-active-active:#006cff;
$color-gray-bg:#f5f5fa;
$colo-gray-bg-alt:#ededf7;
$color-gray-text:#b3b3ba;
$color-gray-separator:#e6e6f0;
$basic-fz-mob: 18px;


%h1-desktop {
  font-size: 42px;
  line-height: 1.2;
}

%h1-mobile {
  font-size: 32px;
  line-height: 1.2;
  font-weight: 700;
  background: linear-gradient(to right, #f0145a, #ef8080);
  -webkit-background-clip: text;
  color: transparent;
}

%h2-desktop {
  font-size: 32px;
  line-height: 1.2;
}

%h2-mobile {
  font-size: 22px;
  line-height: 1.2;
  font-weight: 500;
}

%h3-desktop {
  font-size: 24px;
  line-height: 1.2;
}

%h3-mobile {
  font-size: 18px;
  line-height: 1.2;
  font-weight: 500;
}

%h3-caps {
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: 1px;
  font-weight: 700;
  text-transform: uppercase;
}

%h4 {
  font-size: 18px;
  line-height: 1;
  font-weight: 500;
}

%paragraph {
  font-size: 18px;
  line-height: 1.4;
}

%introduction {
  font-size: 21px;
  line-height: 1.4;  
}

%time {
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 1px;
  color: #b3b3ba;
}

%center {
  max-width:1360px;
  margin: 0 auto;
  padding: 0 20px;
}

%galery_ul {
  list-style: none;
  display: flex;
  justify-content: center;
    li {
      opacity: 0.2;
        &.active {
          opacity: 1;
        }
        &::before {
          content: '';
          display: block;
          width: 16px;
          height: 16px;
          border-radius: 50%;
          background-color: $color-active-regular;
          margin: 0 10px;
        }
      }
}

%arrow {
  display: inline-block;
  width: 10px;
  height: 10px;
  border: solid;
  margin-left: 3px;
  border-width: 2px 2px 0 0;
  transform: rotate(45deg)
}

%button {
  display: block;
  width: 250px;
  padding: 16px 5px;
  margin: 0 auto;
  border-radius: 12px;
  color: #fff;
  border: none;
  background-color: $color-active-regular;
  box-shadow: 0 0 20px rgba(36, 163, 255, 0.5);
  font-size: $basic-fz-mob - 4px;
  font-weight: 700;
  cursor: pointer;

  &:hover {
    background-color: $color-active-hover;
  }

  &:active {
    background-color: $color-active-active;
  }
}