.contact {
    padding: 20px 20px 40px;
    font-size: $basic-fz-mob + 3px;

    @media (min-width: 768px) {
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding-top: 40px;
    }

    @media (min-width: 1024px) {
        padding: 80px 20px;
    }

    address {
        font-style: normal;
        &::first-line {
            font-weight: 500;

            @media (min-width: 768px) {
                font-weight: 400;
            }
        }
    }

    .info {
        color: $color-main;

        @media (min-width: 450px) {
            text-align: center;
        }

        @media (min-width: 768px) {
            text-align: left;
            width: 40%;
        }

        @media (min-width: 1024px) {
            width: 412px;
        }

        a.facebook {
            font-size: $basic-fz-mob - 6px;
            font-weight: 700;
            letter-spacing: 1px;
            margin-left: 10px;
        }

        .address {
            padding-bottom: 20px;
        }

        .link {
            padding: 20px 0 40px;
            display: flex;
            align-items: center;

            @media (min-width: 450px) {
                justify-content: center;
                text-align: left;
            }

            @media (min-width: 768px) {
                justify-content: flex-start;
            }
        }
    }
}

.info {
    .contact_us {
        padding: 40px 0;
        border: solid $color-gray-separator;
        border-width: 4px 0;

        a {
            display: block;
            margin-bottom: 20px;
            
            &:last-child {
                margin: 0;
            }
        }

        a[href*="tel"] {
            color: $color-main-alt;
        }
    }
}

.contact_form {
    background-color: $color-gray-bg;
    //width: 375px;
    //max-width: 527px;
    margin-left: -20px;
    margin-right: -20px;
    padding: 20px;
    color: $color-gray-text;

    @media (min-width: 450px) {
        text-align: center;
    }

    @media (min-width: 768px) {
        text-align: left;
        margin: 0;
        width: 375px;
        border-radius: 12px;
    }

    @media (min-width: 1024px) {
        width: 527px;
    }

    h1 {
        @extend %h2-mobile;
        color: $color-accent;
        margin-bottom: 20px;
        font-weight: 500;

        @media (min-width: 1024px) {
            background: linear-gradient(to right, #f0145a, #ef8080);
            -webkit-background-clip: text;
            color: transparent;
            font-weight: 700;
            font-size: $basic-fz-mob + 14px;
        }
    }

    h3 {
        @extend %h3-caps;
        margin-bottom: 22px;
    }

    p {
        margin: 0 0 20px;

        &:nth-last-of-type(2) {
            margin-bottom: 8px;

            @media (min-width: 1024px) {
                margin-bottom: 20px;
            }
        }
    }

    input,
    textarea {
        width: 335px;
        padding: 10px;
        border-radius: 8px;
        border: 1px solid transparent;
        font-size: $basic-fz-mob;
        outline: none;

        &:focus {
            border-color: #000;
        }

        &::placeholder {
            color: $color-gray-text;
        }

        @media (min-width: 1024px) {
            width: 487px;
        }
    }

    textarea {
        height: 180px;
        resize: none;
    }

    button {
        @extend %button;
    }
}

.map {
    iframe {
        width: 100%;
        height: 600px;
        display: block;
    }
}