
//banner bg start//

.banner_bg {
    background: linear-gradient(45deg, #323264 0%, #323264 50%, #7A425E 100%);
    color: #fff;

    @media (min-width: 700px) {
        background: linear-gradient(32deg, #323264 0%, #323264 50%, #7A425E 100%);
    }

    @media (min-width: 1024px) {
        background: url("../images/mnogoP.png") 55% 50% no-repeat, linear-gradient(21deg, #323264 0%, #323264 50%, #7A425E 100%);
    }
}


.banner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 95vh;
    //padding-top: 100px;
    padding-bottom: 15px;
    position: relative;
    background: url("../images/Shade.png") no-repeat, url("../images/SymbolLongleg.png") no-repeat;
    background-position: 50% 100%;

    @media (orientation: landscape) and (min-width:375px) and (max-width: 1000px) {
        background-size: auto 70%;
    }

    @media (min-height: 700px) {
        background:url("../images/Shade.png") 50% 90% no-repeat, url("../images/SymbolLongleg.png") 50% 80% no-repeat;
    }

    @media (min-height: 1024px) {
        background:url("../images/Shade.png") 100% 72% no-repeat, url("../images/SymbolLongleg.png") 50% 60% no-repeat;
    }


    a.button {
        width: 42px;
        height: 42px;
        border-radius: 50%;
        border: 1px solid #fff;
        position: absolute;
        bottom: 15px;
        left: 50%;
        transform: translate(-50%);
        display: flex;
        justify-content: center;
        align-items: center;
    
        &:hover {
            background-color: $color-accent;
        }
        
        span {
            width: 15px;
            height: 15px;
            border: solid #fff;
            border-width: 2px 2px 0 0;
            transform: rotate(135deg)
        }
    }
}

.banner_text {
    text-align: center;

    @media (min-width: 500px){
        max-width: 460px;
    }
    h1 {
        font-size: $basic-fz-mob + 20px;
        line-height: 1.2;
        font-weight: 500;
    }
}

//banner bg end//

//button_info start//

.button_info {
    color: #fff;
    padding-top: 40px;
    padding-bottom: 5px;

    @media (min-width: 1024px) {
        display: flex;
        flex-direction: row-reverse;
        justify-content: center;
        align-items: center;
        font-size: $basic-fz-mob +3px;
        padding-top: 80px;
    }
}

.main_info {
    @media (min-width: 1024px) {
        max-width: 600px;
    }

    .title_main {
        @extend %h3-caps;
        background: linear-gradient(to right, #ef8080, #f0145a);
        -webkit-background-clip: text;
        color: transparent;
        margin-bottom: 10px;

        @media (min-width: 1024px) {
            display: none;
        }
    }

    h2 {
        @extend %h2-mobile;

        @media (min-width: 1024px) {
            font-size: $basic-fz-mob + 14px;
            line-height: 1.2;
        }
    }
}

.list_bullet {
    list-style: none;
    display: flex;
    justify-content: center;

    @media (min-width: 1024px) {
        display: block;
        margin-right: 40px;
        font-weight: 700;
        max-width: 527px;
    }

    li {
        font-size: 0;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background-color: $color-active-regular;
        margin-right: 14px;
        opacity: 0.2;
        color: $color-gray-text;

        @media (min-width: 1024px) {
            width: auto;
            height: auto;
            border-radius: 0;
            background: none;
            opacity: 1;
            font-size: 42px;
            line-height: 1.2;
            padding: 10px 40px 10px 0;
            margin: 0;
            text-align: right;
            border-right: 4px solid $color-gray-separator;

            &:hover {
                background: linear-gradient(to right, #ef8080, #f0145a);
                -webkit-background-clip: text;
                color: transparent;
                cursor: pointer;
            }
        }

        &:last-child {
            padding-bottom: 0;
        }

        &.active {
            opacity: 1;
            pointer-events: none;

            @media (min-width: 1024px) {
                background: linear-gradient(to right, #ef8080, #f0145a);
                -webkit-background-clip: text;
                color: transparent;
                border-color: $color-accent;
            }
        }
    }
}

//button_info end//

//service start//

.services_bg {
    margin-top: -1px;
    color: #fff;
    background: Linear-gradient(215deg, #323264 0%, #323264 59.9%, #643E72 100%);
    border-bottom-right-radius: 12px;
    border-bottom-left-radius: 12px;
    position: relative;
}

.services_bg::before{
    content: "";
    width: 97%;
    height: 100%;
    position: absolute;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    background: #CACADB;
    z-index: -1;
    border-radius: 0 0 12px 12px;
  }
  
  .services_bg::after{
    content: "";
    width: 94%;
    height: 100%;
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    background: #E7E7F2;
    z-index: -2;
    border-radius: 0 0 12px 12px;
  }

.services {
    padding-top: 40px;
    font-size: $basic-fz-mob - 2px;

    @media (min-width: 768px) {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
    }

    @media (min-width: 1024px) {
        font-size: $basic-fz-mob;
        line-height: 1.44;
        padding-bottom: 22px;
        padding-top: 75px;
    }
    >h3 {
        @extend %h3-caps;
        opacity: 0.5;
        margin-bottom: 3px;

        @media (min-width: 768px) {
            width: 100%;
        }
    
        a {
            color: inherit;

            &:hover {
                color: $color-accent;
            }
        }

        span {
            @extend %arrow;
        }
    }

    h2 {
        @extend %h1-mobile;
        margin-bottom: 20px;

        @media (min-width: 768px) {
            width: 100%;
            margin-bottom: 63px;
        }

        @media (min-width: 1170px) {
            font-size: 42px;
            line-height: 1.2;
        }
    }
}

.services_card {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 24px;

    @media (max-width: 767px) {
        &:last-child {
            margin: 0;
        }
    }
    
    @media (min-width: 768px) {
        width: 50%;
        padding-right: 15px;
    }

    @media (min-width: 1170px) {
        width: 33.33%;
        padding: 0 38px 0 0;
        flex-direction: column;
        flex-wrap: nowrap;
        align-items: flex-start;
        margin-bottom: 58px;
    }

    h3 {
        @extend %h3-mobile;
        margin-left: 10px;
        max-width: calc(100% - 74px);
        
        @media (min-width: 1170px) {
            max-width: 100%;
            margin: 0 0 15px;
            font-size: 24px;
            line-height: 1.2;
        }
    }

    p {
        width: 100%;
        margin-top: 10px;

        @media (min-width: 1170px) {
            margin-top: 0;
        }
    }
}

.block_for_img {
    width: 64px;
    height: 64px;
    border-radius: 18px;
    background: #fff;
    display: inline-flex;
    justify-content: center;
    align-items: center;

    @media (min-width: 1170px) {
        width: 92px;
        height: 92px;
        margin-bottom: 20px;
    }

    img {
        max-width: 44px;

        @media (min-width: 1170px) {
            max-width: 64px;
        }
    }
}

//service end//

//team start//

.team_main {
    color: $color-gray-text;
    font-size: $basic-fz-mob - 2px;
    padding-top: 40px;

    @media (min-width: 1024px) {
        padding-top: 60px;
    }

    ul {
        @extend %galery_ul;
    }   
}

.team_person_main {
    background-color: $color-gray-bg;
    border-radius: 12px;
    margin: 0 20px 15px;
    padding: 20px;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    @media (min-width: 900px) {
        display: block;
        margin: 0 auto 15px;
        padding: 0 40px 0 0;
    }

    h3 {
        @extend %h3-caps;
        width: 100%;

        @media (min-width: 900px) {
            margin-top: 40px;
        }
    }

    h2 {
        @extend %h1-mobile;
        margin-bottom: 10px;
        width: 100%;

        @media (min-width: 1024px) {
            margin-bottom: 20px;
            font-size: 42px;
            line-height: 1.2;
        }
    }

    .person_card_text {
        order: 2;
    }

    h4 {
        @extend %h4;
    }

    p {
        margin: 5px 0;
        //margin: -35px 0 0 69px;
    }

    .person_card_main {
        margin-right: 10px;
        max-width: 60px;
        order: 1;

        img {
            display: block;
        }

        @media (min-width: 900px) {
            float: left;
            max-width: 413px;
            margin-right: 40px;
        }
    }

    span {
        @extend %arrow;
    }
}

//team end//

//cases_main start//

.cases_main {
    color: $color-gray-text;
    padding-top: 30px;

    @media (min-width: 700px) {
        padding-top: 40px;    
    }
}

.cases.center {
    @media (min-width: 900px) {
        padding: 0;
    }
}

.cases {
    @media (min-width: 835px) {
        display: grid;
        grid-template-rows: 57px repeat(2, minmax(198px, 1fr));
        grid-template-columns: minmax(265px, 1fr) minmax(0, 113px) minmax(265px, 1fr) minmax(0, 113px) minmax(265px, 1fr);
        grid-template-areas: "post-1 post-1 post-3 post-3 post-4"
                             "post-2 post-2 post-3 post-3 post-4"
                             "post-5 post-6 post-6 post-7 post-7";
        .cases_card1 {
            grid-area: post-1;
        }
  
        .cases_card2 {
            grid-area: post-2;
        }
  
        .cases_card3 {
            grid-area: post-3;
        }
  
        .cases_card4 {
            grid-area: post-4;
        }
    
        .cases_card5 {
            grid-area: post-5;
        }
    
        .cases_card6 {
            grid-area: post-6;
        }
    
        .cases_card7 {
            grid-area: post-7;
        }                     
    }

    @media (min-width: 1000px) {
        grid-template-columns: minmax(320px, 1fr) minmax(0, 113px) minmax(320px, 1fr) minmax(0, 113px) minmax(320px, 1fr);
    }

    [class^="cases_card"] {
        background-color: $color-gray-bg;
        margin-bottom: 2px;
        padding: 20px;

        @media (min-width: 835px) {
            margin: 2px;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
        }
        &:first-child {
            border-top-left-radius: 12px;

            h2 {
                @extend %h3-caps;
                color: $color-accent;
            }
        }

        &:nth-child(4) {

            @media (min-width:835px) {
                border-radius: 0 12px 0 0;
            }
        }

        &:nth-child(5) {

            @media (min-width:835px) {
                border-radius: 0 0 0 12px;
            }
        }

        &:last-child {
            border-radius: 0 0 12px 12px;

            @media (min-width:835px) {
                border-radius: 0 0 12px 0;
            }
        }

        h3 {
            @extend %h3-mobile;
            color: $color-main-soft;

            @media (min-width: 1000px) {
                font-size: 24px;
                line-height: 1.2;
            }
        }

        p {
            &:last-child {
                margin-bottom: 0;
            }
        }
    }   
}

.brand {
    text-align: center;
    padding-top: 30px;
    display: flex;
    flex-wrap: wrap;
    h3 {
        @extend %h3-caps;
        color: $color-main;
        margin-bottom: 30px;
        width: 100%;
    }

    .brand_logo {
        width: 50%;
        height: 90px;
        padding: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        
        a{
            &:hover {
                opacity: 0.65;
            }
        }

        @media (min-width: 768px) {
            width: 33.33%;
        }

        @media (min-width: 1024px){
            width: 25%;
            height: 120px;
            padding: 13px 70px;
        }
    }
}

//cases_main end//

//publications_main start//

.publications_main {
    font-size: $basic-fz-mob - 2px;
    padding: 20px 0 40px;
    background: $color-gray-bg;
    height: 908px;
    overflow: hidden;
    position: relative;

    @media (min-width: 850px) {
        padding-top: 40px;
        height: 872px;
    }
}

.publications {
    h3 {
        @extend %h3-caps;

        span {
            @extend %arrow;
        }
    }

    h2 {
        @extend %h1-mobile;
        margin-bottom: 20px;

        @media (min-width: 1024px) {
            font-size: 42px;
            line-height: 1.2;
            margin-bottom: 40px;
        }
    }

    .button_bg {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 40px 0;
        background-color: $color-gray-bg;
        z-index: 2;

        @media (min-width: 650px) {
            background: linear-gradient(rgba(245, 245, 250, 0) 0%, #F5F5FA  86.5%);
        }
    }

    button {
        @extend %button;
    }
}

.publications_cards {
    @media (min-width: 650px) {
        columns: 2;
    }

    @media (min-width: 850px) {
        columns: 3;
    }
}

.publications_card {
    max-width: 335px;
    background: #fff;
    border-radius: 8px;
    overflow: hidden;
    margin: 0 auto 20px;
    break-inside: avoid;

    @media (min-width: 650px){
        margin: 0 auto 40px;
    }

    @media (min-width: 950px) {
        max-width: 414px;
    }

    img {
        display: block;
        margin: 0 auto;
    }

    .text{
        padding: 20px;

        h4 {
            @extend %h4;
            margin-bottom: 10px;
        }
    
        time {
            display: block;
            @extend %time;

            @media (min-width: 850px) {
                margin-bottom: 20px;
            }
        }
    
        p {
            margin: 0 0 20px;
            display: none;

            &:first-of-type {
                padding-top: 20px;
                border-top: solid 3px $color-gray-separator;
            }

            &:last-of-type {
                margin-bottom: 0;
            }

            @media (min-width: 850px) {
                display: -webkit-box;
                -webkit-line-clamp: 3;
                -webkit-box-orient: vertical;
                overflow-y: hidden;
            }
        }
    }    
}


//publications_main end//