.profile {
    padding: 30px 20px 20px;

    @media (min-width: 950px) {
        display: flex;
        justify-content: center;
        padding: 45px 20px 60px;
    }

    @media (min-width: 1360px) {
        padding-top: 80px;
    }

    .photo {
        max-width: 413px;
        border-radius: 12px;
        overflow: hidden;

        @media (min-width: 950px) {
            width: 413px;
            margin-right: 40px;
        }
    }

    h1 {
        @extend %h1-mobile;
        margin-bottom: 13px;
    }

    .info {

        @media (min-width: 950px) {
            width: 50%;
        }

        .name {
            padding: 20px 0 10px;
            border-bottom: solid 4px $color-gray-separator;

            @media (min-width: 1000px) {
                padding-bottom: 20px;
            }
        }

        span {
            font-size: $basic-fz-mob - 2px;
            color: $color-gray-text;
            display: block;
            margin-bottom: 11px;

            @media (min-width: 1000px) {
                display: inline-block;
                margin: 0 26px 0 0;
            }
        }

        a {
            display: block;
            font-size: $basic-fz-mob - 2px;
            margin-bottom: 10px;

            @media (min-width: 1000px) {
                display: inline-block;
                margin: 0 26px 0 0;
            }
        }
    }

    .text {
        padding-top: 20px;
    
        h3 {
            @extend %h3-caps;
            margin-bottom: 10px;
            color: $color-main;

            @media (min-width: 1100px) {
                margin: 40px 0 10px;
            }
        }

        p {
            margin-top: 0;
        }
    }
}