.team {
    padding: 28px 20px 20px;

    @media (min-width: 1024px) {
        padding: 85px 20px 20px;
    }

    .all_person {
        @media (min-width: 700px) {
            display: flex;
            flex-wrap: wrap;
        }

        @media (min-width: 1024px) {
            justify-content: space-around;
        }

        @media (min-width: 1360px) {
            justify-content: space-between;
        }
    }

    h1 {
        @extend %h1-mobile;
        margin-bottom: 20px;

        @media (min-width: 1024px) {
            max-width: 864px;
            font-size: 42px;
            line-height: 1.2;
            margin: 0 auto 80px;
        }
    }
}


.team_person {
    font-size: $basic-fz-mob - 2px;
    color: $color-gray-text;
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px;

    @media (min-width: 700px) {
        width: 50%;

        &:nth-child(even) {
            padding-left: 5px;
        }

        &:nth-child(odd) {
            padding-right: 5px
        };
    }

    @media (min-width: 1024px) {
        width: 413px;
        display: block;
        margin: 0 10px 40px;

        &:nth-child(even) {
            padding: 0;
        }

        &:nth-child(odd) {
            padding: 0;
        }
    }

    @media (min-width: 1360px) {
        margin: 0 0 40px;
    }

    .photo_person {
        border-radius: 12px;
        overflow: hidden;
        width: 158px;
        height: 191px;
        margin-right: 20px;

        @media (min-width: 1024px) {
            width: 100%;
            height: 500px;
            margin: 0 0 20px;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .info {
        max-width: calc(100% - 178px); // это для того чтобы перенослась фамилия если она будет очень длинной
        word-wrap: break-word; // это для того чтобы перенослась фамилия если она будет очень длинной
        
        @media (min-width: 1024px) {
            max-width: 100%;
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        p {
            margin: 5px 0 10px;
        }

        a.button {
            width: 32px;
            height: 32px;
            border-radius: 50%;
            background-color: $color-gray-bg;
            display: flex;
            justify-content: center;
            align-items: center;
        
            &:hover {
                background-color: rgb(182, 226, 255);
            }
            
            span {
                width: 10px;
                height: 10px;
                border: solid $color-active-regular;
                border-width: 2px 2px 0 0;
                transform: rotate(45deg);
            }
        }
    }

    h4{
        @extend %h4;
        color: $color-main;

        a {
            color: inherit;

            &:hover {
                color: $color-accent;
            }
        }
    }
}   