.publications_page.margin_main {
    margin-top: 120px;

    @media (min-width: 768px) {
        margin-top: 210px;
    }

    @media (min-width: 1024px) {
        margin-top: 206px;
    }
}

.publications_page {
    .publications_main {
        border-top-left-radius: 12px;
        border-top-right-radius: 12px;
        height: 1869px;
        padding: 22px 0 40px;

        @media (min-width: 850px) {
            height: auto;
            padding-top: 42px;
        }
    }

    .switch-button {
        background: $color-gray-separator;
        border-radius: 12px;
        overflow: hidden;
        width: 268px;
        text-align: center;
        font-size: $basic-fz-mob;
        letter-spacing: 1px;
        color: $color-main;
        position: relative;
        padding: 2px 130px 2px 2px;
        position: relative;
        margin: 0 auto 22px;
        font-weight: 500;

        @media (min-width: 850px) {
            margin-bottom: 40px;
        }
      
        &:before {
          content: "компанія";
          position: absolute;
          top: 2px;
          bottom: 2px;
          right: 2px;
          width: 126px;
          border-radius: 10px;
          display: flex;
          align-items: center;
          justify-content: center;
          z-index: 3;
          pointer-events: none;
        }
      
        &-checkbox {
          cursor: pointer;
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          opacity: 0;
          z-index: 2;
      
          &:checked + .switch-button-label:before {
            transform: translateX(127px);
            transition: transform 300ms linear;
          }
      
          & + .switch-button-label {
            position: relative;
            padding: 6px 0;
            display: block;
            user-select: none;
            pointer-events: none;
      
            &:before {
              content: "";
              background: #fff;
              height: 100%;
              width: 100%;
              position: absolute;
              left: 0;
              top: 0;
              border-radius: 10px;
              transform: translateX(0);
              transition: transform 300ms;
            }
      
            .switch-button-label-span {
              position: relative;
            }
          }
        }
    }

    .button_bg {
        @media (min-width: 850px) {
            padding: 60px 0;
        }
    }
}