.publications_entry {

    .publication_entry {
        padding-top: 30px;
        display: flex;
        justify-content: center;

        @media (min-width: 768px) {
            padding-top: 60px;
        }
    }

    .publications_card_entry {
        max-width: 909px;
        margin: 0 -20px;

        img {
            display: block;
            margin: 0 auto;
        }

        .text{
            padding: 20px 20px 20px;

            @media (min-width: 768px) {
                padding: 40px 20px 40px;
            }
    
            h2 {
                @extend %h2-mobile;
                margin-bottom: 10px;
                color: $color-main;

                @media (min-width: 768px) {
                    font-size: 32px;
                    line-height: 1.2;
                }
            }

            h1 {
                @extend %h1-mobile;
                margin: 0 0 40px;

                @media (min-width: 768px) {
                    font-size: 42px;
                    line-height: 1.2;

                    margin: 40px 0;
                }
            }

            h3 {
                @extend %h3-mobile;
                color: $color-main;
                margin-bottom: 20px;

                @media (min-width: 768px) {
                    font-size: 24px;
                    line-height: 1.2;
                    margin-bottom: 18px;
                }
            }
            
            ol + p {
                @media (min-width: 768px) {
                    margin-bottom: 40px;
                }
            }

            span {
                display: block;
                margin: 0 auto;
                text-align: center;
                font-size: $basic-fz-mob - 2px;
                color: $color-gray-text;
                margin: 10px 0 20px;

                @media (min-width: 768px) {
                    margin: 15px 0 40px;
                }
            }
        
            time {
                display: block;
                @extend %time;
                margin-bottom: 20px;

                @media (min-width: 768px) {
                    margin-bottom: 40px;
                }
            }
            
            span + p {
                margin-bottom: 40px;
            }

            p {
                display: block;

                &:first-of-type {
                    padding-top: 20px;
                    border-top: solid 3px $color-gray-separator;

                    @media (min-width: 768px) {
                        padding-top: 40px;
                    }
                }

                &:last-of-type {
                    padding-bottom: 40px;
                    border-bottom: solid 3px $color-gray-separator;
                    margin: 0;
                }
            }
        }
    }

    .icons {
        padding: 20px 0;
        display: flex;
        justify-content: center;

        @media (min-width: 768px) {
            display: block;
        }

        .icon {
            width: 42px;
            height: 42px;
            margin-right: 10px;
            border-radius: 12px;
            background: $color-gray-bg;
            padding: 5px;
            display: inline-flex;
            justify-content: center;
            align-items: center;

            &:hover {
                background:rgba(224, 224, 224, 0.808);
            }
        }
    }

    .publications_main {
        border-top-left-radius: 12px;
        border-top-right-radius: 12px;
    }
}