//header start//

.header {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    padding-top: 22px;
    z-index: 8;

    @media (min-width: 768px) {
        padding-top: 34px;
    }

    @media (min-width: 1360px) {
        padding-top: 51px;
    }
}

.header_flex {
    position: relative;

    @media (min-width: 768px) {
        color: #fff;
    }
}

.header_flex.black_header {
    @media (min-width: 768px) {
        color: $color-gray-text;
    }
}

.logo {
    position: absolute;
    left: 20px;
    top: 46px;
    display: flex;

    span {
        text-transform: lowercase;
        font-size: 19px;
        line-height: 1.2;
        color: #fff;

        @media (min-width: 768px){
            font-size: 38px;
        }
    }

    img[alt="slash"] {
        width: 11px;
        height: 24px;

        @media (min-width: 768px) {
            width: 21px;
            height: 46px;
        }
    }

    img[alt="symboll"] {
        width: 21px;
        height: 24px;

        @media (min-width: 768px) {
            width: 41px;
            height: 47px;
        }
    }

    @media (min-width: 1360px) {
        top: 0;
    }
}

.black_header {
    .logo {
        span {
            color: $color-main;
        }
    }
}

.top_info {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 25px;
    font-weight: 700;
    a {
        color: $color-gray-text;

        @media (min-width: 768px) {
            color: inherit;
        }
    }

    @media (min-width: 768px) {
        justify-content: flex-start;
    }

    @media (min-width: 1024px) {
        justify-content: flex-end;
    }
}

.lang {
    font-size: $basic-fz-mob - 6px;
    margin-left: -4px;

    ul {
        list-style: none;
        display: flex;

        .active_lang a {
            background-color: $color-accent;
            color: #fff;
        }

        a {
            border-radius: 8px;
            padding: 5px 6px;
            &:hover {
                background-color: $color-gray-separator;
            }
        }   
    }   
}

.address_tel {
    font-size: $basic-fz-mob - 2px;

    @media (min-width: 1024px) {
        margin-right: 20px;
    }

    address {
        display: inline;
        font-style: normal;
    }

    a.address,
    a.icon {
        display: none;

        @media (min-width: 768px) {
            display: inline;
        }
    }

    a.address {
        font-weight: 400;
    }
    
    a {
        @media (min-width: 768px) {
            margin-left: 20px;
        }

        &:hover {
            color: $color-active-hover;
        }
    }
}

.black_header {
    .header_nav {
        li {
            @media (min-width: 768px) {
                color: $color-active-regular;
    
                &:hover {
                    color: $color-accent;
                    border-bottom: none;
                    //border-bottom: 3px solid $color-accent;
                }
            }
        }
    }

    .nav_menu {
        .active_page {
            pointer-events: none;
    
            @media (min-width: 768px) {
                color: $color-accent;
                border-color: $color-accent;
                //border-bottom: 3px solid $color-accent;
            }
        }
    }

    label[class=burger_open] {

        &:active {
            background-color: #EDEDF7;
        }
        
        span {
            background: $color-main;
        }
    }
}


.header_nav {
    display: flex;
    flex-direction: row-reverse;
    font-weight: 500;
    //width: 100%;

    a {
        @media (min-width: 768px) {
            color: inherit;

            // &:hover {
            //     border-bottom: 3px solid #fff;
            // }
        }
    }

    @media (min-width: 768px) { 
        flex-direction: row;
        margin-top: 93px;
        margin-left: -1px;
    }

    @media (min-width: 1024px) {
        flex-direction: row-reverse;
        margin: 35px 0 0;
    }

    @media (min-width: 1360px) {
        margin: 20px 0 0;
    }
}

.nav_menu {
    visibility: hidden;
    opacity: 0;
    padding: 15px 0;                  
    height: 0;                               
    //transition: opacity 0.4s linear;

    .active_page {
        pointer-events: none;

        @media (min-width: 768px) {
            border-color: #fff;
            //border-bottom: 3px solid #fff;
        }
    }

    @media (min-width: 768px) {
        visibility: visible;
        opacity: 1;
        height: auto;
        padding: 0;
    }

    img {
        @media (min-width: 768px) {
            display: none;
        }
    }

    ul {
        list-style: none;

        @media (min-width: 768px) {
            display: flex;
            
            li {
                margin-right: 15px;
                border-bottom: 3px solid transparent;

                &:hover {
                    border-color: #fff;
                }
            }
        }
    }
}

label[class=burger_open] {
    display:flex;
    flex-direction:column;
    width: 30px;
    position: relative;
    z-index: 11;
    cursor: pointer;
    border-radius: 12px;
    padding: 6px;

    @media (min-width: 768px) {
        display: none;
    }

    span {
        background: #fff;
        border-radius:10px;
        height: 2px;
        margin: 2px 0;
        width: 100%;
    }
}

#burger_nav {
  display: none;
}

@media (max-width: 767px) {

    #burger_nav:checked ~ .nav_menu {
        visibility: visible;
        opacity: 1;
        transition: opacity 0.4s linear;
        width: 100%;
        height: 100vh;
        position: absolute;
        z-index: 12;
        top: -22px;
        left: 0;
        text-align: center;
        background-color: #fff;
        
        .active_page {
            border-bottom: none;
            color: $color-accent;

            a {
                color: inherit;
            }
        }

        img {
            margin-bottom: 15px;
        }
    
        a {
            display: block;
            padding: 15px 0;
            font-size: 24px;
            line-height: 28px;

            &:active {
                background-color: #EDEDF7;
            }
        }
    
        li {
            background-color: $color-gray-bg;
            margin-bottom: 1px;
        }
        
    }
    
    .close_form {
        position: absolute;
        top: 15px;
        right: 15px;
        cursor: pointer;
        padding: 3px 4px;
        border-radius: 12px;

        &:active {
            background-color: #EDEDF7;
        }
    }
    
    .close_form:before {
        transform: translateY(6px) rotate(135deg);
    }
    
    .close_form:after {
        transform: translateY(-6px) rotate(-135deg);
    }
    
    .close_form span{
        transform: scale(0);
    }
    
    .close_form span,
    .close_form:after,
    .close_form:before{
      background: #EC6F5D;
      content: "";
      width: 18px;
      display: block;
      height: 2px;
      margin: 4px 0;
    }
}
//header end//