
.about_us {
    padding-top: 20px;
    padding-bottom: 14px;

    @media (min-width: 1024px) {
        font-size: $basic-fz-mob + 3px;
    }

    @media (min-width: 1360px) {
        padding-top: 80px;
        padding-bottom: 100px;
    }
}

.about_info {
    @media (min-width: 768px) {
        display: flex;
        align-items: flex-start;
        justify-content: center;
    }

    .text {
        @media (min-width: 768px) {
            max-width: 500px;
        }

        @media (min-width: 1024px) {
            max-width: 641px;
            margin-right: 34px;
        }
    }

    h1 {
        @extend %h1-mobile;

        @media (min-width: 1024px) {
            font-size: 42px;
            line-height: 1.2;
        }
    }

    .regal {
        border-top: 3px solid $color-gray-separator;
        padding-top: 15px;

        @media (min-width: 768px) {
            border-top: none;
            border-left: 3px solid $color-gray-separator;
            padding: 0 0 0 20px;
        }

        @media (min-width: 1024px){
            max-width: 390px;
        }

        @media (min-width: 1360px) {
            span {
                margin-bottom: 10px;
            }

            p {
                margin: 0 0 40px;
            }
        }

        img {
            max-width: 32px;
            display: inline-block;
            vertical-align: sub;
        }

        span {
            font-size: 36px;
            font-weight: 700;
            line-height: 1.2;
            background: linear-gradient(to right, #f0145a, #ef8080);
            -webkit-background-clip: text;
            color: transparent;
        }

        p {
            color: $color-main;
        }
    }
}

.team_pic {
    max-width: 1360px;
    margin: 0 auto;
    background: linear-gradient(to left, #F01432 0%, #323264 29.69%);
    position: relative;
    img {
        display: block;
        object-fit: cover;
        min-height: 290px;
        opacity: 0.5;
        -webkit-filter: grayscale(100%);
        -moz-filter: grayscale(100%);
        -ms-filter: grayscale(100%);
        -o-filter: grayscale(100%);
        filter: grayscale(100%);
        filter: gray; /* IE 6-9 */ //это для обесцвецивания картинки

        // картинка скачивается сразу с градиентом, поэтому при обесцвечивании и надожении градиента -
        // цвет получается немного другой. Так же из-за того что она идёт сказу с градиентом, то в моб версии из-за object-fit: cover; его не видно, а как сделать это с настоящим градиентом, я не смог придумать
    }

    span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 36px;
        font-weight: 700;
        letter-spacing: 20px;
        text-shadow: 0 15px 30px rgba(40, 40, 86, 0.8);
        color: #fff;
        opacity: 0.8;

        @media (min-width: 768px) {
            font-size: 66px;
            line-height: 1.2;
            letter-spacing: 43px;
        }

        @media (min-width: 1024px) {
            font-size: 96px;
            letter-spacing: 63px;
        }
    }
}

.tagline {
    padding: 20px 20px 40px;

    @media (min-width: 768px) {
        padding-top: 40px;
        display: flex;
        align-items: flex-start;
        justify-content: center;
    }

    @media (min-width: 1024px) {
        padding: 80px 20px 93px;
    }

    h2 {
        @extend %h1-mobile;

        @media (min-width: 1024px) {
            font-size: 42px;
            line-height: 1.2;
            max-width: 414px;
            margin-left: 20px;
        }
    }

    .text {
        @media (min-width: 768px) {
            max-width: 500px;
            padding-right: 20px;
        }

        @media (min-width: 1024px) {
            padding-right: 20px;
            max-width: 640px;
            font-size: $basic-fz-mob + 3px;
        }

        p {
            &:first-of-type{
                margin-top: 0;
            }
        }
    }
}

.achievements {
    background-color: $color-gray-bg;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
}

.achievements_cards {
    padding: 30px 20px;
    text-align: center;

    h3 {
        @extend %h3-caps;
        margin-bottom: 30px;
    }

    .block_card {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        @media (min-width: 400px) {
            justify-content: space-evenly;
        }

        @media (min-width: 1360px) {
            justify-content: space-between;
        }
    }

    .card {
        display:flex;
        justify-content: center;
        align-items: center;
        width: 160px;
        height: 57px;
        border-radius: 12px;
        background-color: #fff;
        padding: 13px 37px;

        @media (min-width: 500px) {
            margin: 0 5px 15px;
        }

        @media (min-width: 1024px) {
            width: 300px;
            height: 109px;
            padding: 25px 53px;
        }

        &:nth-of-type(n+3) {
            display: none;

            @media (min-width: 500px) {
                display: flex;
            }
        }
    }

    ul {
        margin-top: 15px;
        @extend %galery_ul;

        @media (min-width: 500px) {
            :nth-child(n+4) {
                display: none;
            }
        }
    }
}   

